.blog {
  display: grid;
  grid-template-areas: "nav hero content";
  grid-template-rows: 100vh;
}
.blog-navigation {
  display: grid;
  overflow-x: hidden;
  overflow-y: auto;
  padding: 80px 0 80px 0;
  -webkit-transition: width 0.8s ease-in-out;
  -o-transition: width 0.8s ease-in-out;
  transition: width 0.8s ease-in-out;
  background-color: white;
  grid-area: nav;
  grid-template-areas: "latest" "categories";
  grid-template-rows: auto
      1fr;
}
.blog-navigation-latest,
.blog-navigation-categories {
  display: -ms-flexbox;
  display: flex;
  margin: 0 2em 0 2em;
  -ms-flex-flow: column;
  flex-flow: column;
}
.blog-navigation-latest-heading,
.blog-navigation-categories-heading {
  padding: 1em;
  text-align: right;
  border-width: 0 0 1px 0;
  border-style: solid;
  border-color: lightgray;
}
.blog-navigation-latest-items,
.blog-navigation-categories-items {
  overflow-x: hidden;
  padding-top: 1em;
}
.blog-navigation-latest-items .blog-link,
.blog-navigation-categories-items .blog-link {
  display: -ms-flexbox;
  display: flex;
  height: 48px;
  -ms-flex-flow: column;
  flex-flow: column;
  -ms-flex-pack: center;
  justify-content: center;
}
.blog-navigation-latest-items .blog-link:hover,
.blog-navigation-categories-items .blog-link:hover {
  cursor: pointer;
}
.blog-navigation-latest {
  grid-area: latest;
}
.blog-navigation-categories {
  grid-area: categories;
}
.blog-navigation#hide {
  width: 25vw;
}
.blog-navigation#show {
  width: 0vw;
}
.blog-hero {
  position: relative;
  display: grid;
  padding: 5vw;
  -webkit-transition: width 0.5s ease-in-out;
  -o-transition: width 0.5s ease-in-out;
  transition: width 0.5s ease-in-out;
  background: -webkit-gradient(linear, right top, left top, from(bisque), to(seashell));
  background: -webkit-linear-gradient(right, bisque, seashell);
  background: -o-linear-gradient(right, bisque, seashell);
  background: linear-gradient(to left, bisque, seashell);
  grid-area: hero;
  -ms-flex-align: center;
  align-items: center;
  -ms-flex-pack: start;
  justify-content: start;
}
.blog-hero-content {
  position: relative;
  display: -ms-flexbox;
  display: flex;
  width: 20vw;
  height: 80vh;
  -ms-flex-flow: column;
  flex-flow: column;
  -ms-flex-align: start;
  align-items: flex-start;
  -ms-flex-pack: end;
  justify-content: flex-end;
}
.blog-hero-content img {
  width: 20vw;
  -o-object-fit: cover;
  object-fit: cover;
}
.blog-hero-content h1 {
  border-width: 0 0 1px 0;
  border-style: solid;
  border-color: lightgray;
}
.blog-hero-content h3 {
  text-align: justify;
}
.blog-hero-content #post-open {
  border-width: 1px;
  border-style: solid;
  border-color: lightgray;
  background-color: white;
  -ms-flex-item-align: end;
  align-self: flex-end;
}
.blog-hero-content * {
  margin: 2.5% 0;
}
.blog-hero#hide {
  width: 65vw;
}
.blog-hero#show {
  width: 25vw;
}
.blog-post {
  position: relative;
  width: 65vw;
  background-color: white;
  grid-area: content;
}
.blog-post-content {
  display: grid;
  overflow-y: auto;
  height: 100%;
}
.blog-post-content-section {
  display: grid;
  grid-template-areas: "heading slider" "text slider";
  grid-template-rows: auto
          1fr;
  grid-template-columns: repeat(2, 1fr);
}
.blog-post-content-section-heading {
  display: -ms-flexbox;
  display: flex;
  margin: 2em 2em 0 2em;
  border-width: 0 0 1px 0;
  border-style: solid;
  border-color: lightgray;
  grid-area: heading;
}
.blog-post-content-section-text {
  padding: 2em;
  text-align: justify;
  grid-area: text;
}
.blog-post-content-section #image-slider {
  padding: 2em;
  grid-area: slider;
}
.blog-post-content-section #image-slider .slider-content {
  display: -ms-flexbox;
  display: flex;
  margin: 1em 0;
  -ms-flex-align: center;
  align-items: center;
}
.blog-post-content-section #image-slider .slider-content .slider-image {
  display: grid;
  overflow: hidden;
  width: 100%;
  grid-template-rows: 1fr auto;
}
.blog-post-content-section #image-slider .slider-content .slider-image img {
  width: 100%;
  height: 240px;
  -o-object-fit: contain;
  object-fit: contain;
}
.blog-post-content-section #image-slider .slider-content .slider-image-caption {
  padding: 1em;
  text-align: justify;
  color: whitesmoke;
  background-color: #333333;
}
.blog-post #post-close {
  position: absolute;
  top: 50%;
  left: -48px;
  background-color: white;
}



