* {
  font-family: "Work Sans", sans-serif;
  margin: 0;
  padding: 0;
  text-decoration: none;
  color: #333;
  border: 0;
  background: none;
}
th,
td {
  text-align: center;
}
h2 {
  white-space: pre;
}
h1 {
  font-family: "Arvo", serif;
  font-size: 3.5rem;
}
h2 {
  font-size: 2rem;
}
h3 {
  font-size: 1.5rem;
}
.app {
  overflow-x: hidden;
  width: 100vw;
  background: -webkit-gradient(linear, right top, left top, from(royalblue), to(turquoise));
  background: -webkit-linear-gradient(right, royalblue, turquoise);
  background: -o-linear-gradient(right, royalblue, turquoise);
  background: linear-gradient(to left, royalblue, turquoise);
}
.app-content {
  -webkit-transition: -webkit-transform 0.2s ease-in-out;
  transition: -webkit-transform 0.2s ease-in-out;
  -o-transition: transform 0.2s ease-in-out;
  transition: transform 0.2s ease-in-out;
  transition: transform 0.2s ease-in-out, -webkit-transform 0.2s ease-in-out;
}
.app-content#hide {
  -webkit-transform: translateX(0);
  -ms-transform: translateX(0);
  transform: translateX(0);
}
.app-content#show {
  -webkit-transform: translateX(400px);
  -ms-transform: translateX(400px);
  transform: translateX(400px);
}
.app #panel-open {
  position: fixed;
  z-index: 2;
  top: 1em;
  left: 1em;
  background: none;
}
@media screen and (min-width: 800px) {
  .app-content#hide {
    -webkit-transform: translateX(0) scale(1, 1);
    -ms-transform: translateX(0) scale(1, 1);
    transform: translateX(0) scale(1, 1);
  }
  .app-content#show {
    -webkit-transform: translateX(400px) scale(0.8, 0.8);
    -ms-transform: translateX(400px) scale(0.8, 0.8);
    transform: translateX(400px) scale(0.8, 0.8);
  }
}



